export interface Anime {
  animeId: string
  title: string
  otherName?: string
  image: string
  episodes?: string
  released?: string
  status?: string
  genres?: string[]
  synopsis?: string
  type?: string
}

// ========================
// ‚úÖ Gogoanime API functions
// ========================

// 1. Top Airing / Trending
export async function getTrendingAnimeGogo(): Promise<Anime[]> {
  try {
    const res = await fetch('https://gogoanime.consumet.org/api/v1/top-airing')
    if (!res.ok) throw new Error('Failed to fetch trending anime')
    const data = await res.json()
    return data.results.map((item: any) => ({
      animeId: item.animeId,
      title: item.title,
      image: item.image,
      episodes: item.episodes
    }))
  } catch (err) {
    console.error(err)
    return []
  }
}

// 2. Popular (simulasi top 25)
export async function getPopularAnimeGogo(): Promise<Anime[]> {
  try {
    const res = await fetch('https://gogoanime.consumet.org/api/v1/top-airing') // Gogoanime ga ada popular API, pakai top airing
    if (!res.ok) throw new Error('Failed to fetch popular anime')
    const data = await res.json()
    return data.results.map((item: any) => ({
      animeId: item.animeId,
      title: item.title,
      image: item.image,
      episodes: item.episodes
    }))
  } catch (err) {
    console.error(err)
    return []
  }
}

// 3. Detail anime
export async function getAnimeDetailGogo(animeId: string): Promise<Anime | null> {
  try {
    const res = await fetch(`https://gogoanime.consumet.org/api/anime-details/${animeId}`)
    if (!res.ok) throw new Error('Failed to fetch anime detail')
    const data = await res.json()
    return {
      animeId: data.animeId,
      title: data.animeTitle,
      otherName: data.otherName,
      image: data.animeImg,
      episodes: data.totalEpisodes,
      released: data.releasedDate,
      status: data.status,
      genres: data.genres,
      synopsis: data.synopsis,
      type: data.type
    }
  } catch (err) {
    console.error(err)
    return null
  }
}

// 4. Search anime
export async function searchAnimeGogo(query: string): Promise<Anime[]> {
  try {
    const res = await fetch(`https://gogoanime.consumet.org/api/v1/search?keyw=${encodeURIComponent(query)}`)
    if (!res.ok) throw new Error('Failed to search anime')
    const data = await res.json()
    return data.results.map((item: any) => ({
      animeId: item.animeId,
      title: item.title,
      image: item.image,
      episodes: item.episodes
    }))
  } catch (err) {
    console.error(err)
    return []
  }
}

