'use client'

import { Suspense } from 'react'
import { Navbar } from '@/components/navbar'
import { AnimeCard } from '@/components/anime-card'
import { getTrendingAnimeGogo, getPopularAnimeGogo } from '@/lib/api'
import { HeroCarousel } from '@/components/hero-carousel'

// ========================
// üé¨ Skeleton Loader Components
// ========================

function HeroSkeleton() {
  return (
    <div className="mt-8 mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <div className="relative h-80 md:h-96 bg-gray-300 animate-pulse rounded-lg" />
    </div>
  )
}

function SkeletonGrid({ count = 5 }: { count?: number }) {
  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 grid grid-cols-2 sm:grid-cols-3 md:grid-cols-3 lg:grid-cols-5 gap-4">
      {Array(count).fill(0).map((_, i) => (
        <div key={i} className="h-56 bg-gray-300 animate-pulse rounded-lg" />
      ))}
    </div>
  )
}

// ========================
// üè† Sections
// ========================

async function HeroSection() {
  const trendingGogo = await getTrendingAnimeGogo()

  // Mapping ke format HeroCarousel
  const animesForCarousel = trendingGogo.map(a => ({
    mal_id: a.animeId,        // ganti animeId -> mal_id
    title: a.title,
    images: { jpg: { image_url: a.image } }  // HeroCarousel pakai images.jpg.image_url
  }))

  return <HeroCarousel animes={animesForCarousel} />
}

async function PopularSection() {
  const popular = await getPopularAnimeGogo()
  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-2xl font-bold text-foreground mb-4">Popular Anime</h2>
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4">
        {popular.map(anime => (
          <AnimeCard key={anime.animeId} anime={anime} />
        ))}
      </div>
    </div>
  )
}

async function TrendingSection() {
  const trending = await getTrendingAnimeGogo()
  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-2xl font-bold text-foreground mb-4">Trending This Week</h2>
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4">
        {trending.map(anime => (
          <AnimeCard key={anime.animeId} anime={anime} />
        ))}
      </div>
    </div>
  )
}

// ========================
// üè† Home Page
// ========================

export default function HomePage() {
  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      {/* Hero */}
      <Suspense fallback={<HeroSkeleton />}>
        <HeroSection />
      </Suspense>

      {/* Popular */}
      <Suspense fallback={<SkeletonGrid count={10} />}>
        <PopularSection />
      </Suspense>

      {/* Trending */}
      <Suspense fallback={<SkeletonGrid count={10} />}>
        <TrendingSection />
      </Suspense>
    </main>
  )
}
